  //添加素材
  var create = function (obj) {
    var _self = this;
    this.H.toAjax(true).then(function (comData) { //data 公用参数

      comData.data = obj;
      _self.A.post("/v1/section/create", comData).then(function (result) {
        _self.link = "";
        _self.lessonName = "";
        obj.section.sectionId = result.data.data.sectionId;
        obj.section.showEditInput = false;
        _self.sections.push(obj.section);
        console.log(_self.sections);
        if (result.data.state.code == 0) _self.$emit("modalAlert", result.data.state.msg, "确定");
      })
    })
  }
  var $sectionList = function(id,type){
    var _self = this ;
    this.H.toAjax(true).then(function(comData){ //data 公用参数
      comData.data = {activityId:id,type:type}; //type 会改变 课程
      _self.A.post("/v1/section/list",comData).then(function(result){

        console.log("素材",result);

        if(result.data.state.code == 0){
          var sections = result.data.data.sectionResult.sections;
          if( sections &&  sections.length > 0){
            sections.forEach(function(obj){ obj.showEditInput = false; })
            console.log(sections);
            _self.sections = sections;
            _self.sectionPages = result.data.data.sectionResult.page ;
          }
        }


      })
    })

  }
  var publish = function (obj,type) {
    var _self = this;
    this.H.toAjax(true)
      .then(function (comData) { //data 公用参数
        comData.data = obj;
        _self.A.post("/v1/attendance/publish", comData)
          .then(function (result) {
            if (result.data.state.code == 0) {
              _self.$emit("modalAlert", result.data.state.msg, "确定");
              _self.$router.push({path: "/admin/punchShow/info", query: {id: _self.id,type:_self.type}});
            } else {
              _self.$emit("modalAlert", result.data.state.msg, "确定");
            }
          })
      })
  }
  var del = function (obj) {
    var _self = this;
    this.H.toAjax(true).then(function (comData) { //data 公用参数

      comData.data = obj;
      _self.A.post("/v1/section/delete", comData).then(function (result) {
        console.log(result);
        console.log(obj);
        if (result.data.state.code == 0) _self.$emit("modalAlert", result.data.state.msg, "确定");
      })
    })

  }

  var edit = function (obj) {
    var _self = this;
    this.H.toAjax(true).then(function (comData) { //data 公用参数

      comData.data = obj;
      _self.A.post("/v1/section/create", comData).then(function (result) {
        console.log(result);
        console.log(obj);
        if (result.data.state.code == 0){
          _self.$emit("modalAlert", "修改成功");
        }
        else{
          _self.$emit("modalAlert", result.data.state.msg);
        }
      })
    })

  }

  var editSection = function (obj) {
    var _self = this;
    this.H.toAjax(true).then(function (comData) { //data 公用参数

      comData.data = obj;
      _self.A.post("/v1/section/delete", comData).then(function (result) {
        console.log(result);
        console.log(obj);
        if (result.data.state.code == 0) _self.$emit("modalAlert", result.data.state.msg, "确定");
      })
    })

  }

  import ImageUpload from "../../common/imageUpload.vue";
  import MultiImageUpload from "../../common/multiImageUpload.vue";
  import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

  export default{
    data(){
      return {
        baseUrl: M.baseImgUrl,
        isUploading: false,
        descriptionImageUploadData: {
          image: [], //数据格式: {"url": "xxxxx", "id": 123}
          url: this.$router.currentRoute.fullPath
        },
        speakerImageUploadData: {
          image: [],
        },



        id: 0,
        type:'',
        lessonName: "", //章节素材
        link: "",
        targetSectionId: 0,
        targetSectionIndex: 0,
        sections: [],
        sectionPages :{},
        beforEditSection :{},
        menus: {
          'menu1' : '编辑',
          'menu2' : '删除'
        },
        showMenus: false,
        attendance: {
          id: 0,
          descriptionImgIds: "",
          description: "",//课程描述
          speaker: "", //主讲人介绍
          speakerImgIds: ""
        },
        data:{
          id: null,
          name: '',
          startTime: 0,
          endTime:0,
          frequencyDays: 0,
          frequencyTimes: 0,
          planDay: 0,          //计划完成课程天数
          money: 0,
          authNum: 0,
          failDay: 0,
          remark: '',
          logoImg: {            //图片对象
            id: 0,
            url: '',            //图片url
            width: 0,           //图片宽度
            height: 0,          //图片高度
            offsetX: 0,         //x轴偏移
            status: 0,          //图片状态
            createBy: 0,        //number
            createTime: 0 //创建时间
          },
          speaker: '',            //讲师介绍
          speakerImgIds: '',      //讲师介绍图片：JsonArray格式
          description: '',        //课程介绍
          descriptionImgIds: '',  //课程介绍图片：JsonArray格式
          process: 0,             //进度：默认0
          notice: '',             //公告
          createBy: '',
          createTime: '',
          updateBy: '',
          updateTime: '',
          status: '',        //状态：starting不能编辑
          successNum : '',//坚持人数
          reward: '' // 奖金
        }

      }
    },
    props: ["punchData","info"],
    components: {
      Flexbox,
      FlexboxItem,
      XTextarea,
      Group,
      XInput,
      XButton,
      Actionsheet,
      ImageUpload: ImageUpload,
      MultiImageUpload: MultiImageUpload
    },
    mounted(){
      document.getElementById("title").innerHTML = "内容补充";
      var self = this;
      var id = this.$route.query.id ,type = this.$route.query.type ;
      this.$emit("updatePunch",function(result){
        self.as.extend(true,self.data,result);
        console.log("合并",self.data);

      });
      if (this.$route.query.id){
        this.data.id =  this.id = this.$route.query.id;
        this.type = type
      }
      $sectionList.call(self,id,type);

      //演讲人图片回填
      if (self.data.speakerImgIds != "" && self.data.speakerImgIds != null) {
        var imageList =  self.data.speakerImageList;
        var ids = Number(self.data.speakerImgIds.split(","));
        imageList.forEach(function (value, index) {
          self.speakerImageUploadData.image.push({id: ids[index], url: value.url});
        });
      }
      //课程介绍图片回填
      if(self.data.descriptionImgIds != null && self.data.descriptionImgIds != "") {
        var imageList = self.data.descriptionImageList;
        var ids = Number(self.data.descriptionImgIds.split(","));
        imageList.forEach(function (value, index) {
          self.descriptionImageUploadData.image.push({id: ids[index], url: value.url});
        });
      }

//      alert(this.id);
    },
//    computed: {
//      speakerImgIds: function () {
//        var ids = [];
//        this.speakerImageUploadData.image.forEach(function (value, key) {
//          ids.push(value.id);
//        });
//        if(ids.length > 6) {
//          ids.splice(6, ids.length - 1);
//        }
//        return ids.join(",");
//      },
//      descriptionImgIds: function () {
//        var ids = [];
//        this.descriptionImageUploadData.image.forEach(function (value, key) {
//          ids.push(value.id);
//        });
//        if(ids.length > 6) {
//          ids.splice(6, ids.length - 1);
//        }
//        return ids.join(",");
//      }
//    },
    methods: {
      countSpeakerImgIds(){
        var ids = [];
        this.speakerImageUploadData.image.forEach(function (value, key) {
          ids.push(value.id);
        });
        if(ids.length > 6) {
          ids.splice(6, ids.length - 1);
        }
        return ids.join(",");
      },
      countDescriptionImgIds(){
        var ids = [];
        this.descriptionImageUploadData.image.forEach(function (value, key) {
          ids.push(value.id);
        });
        if(ids.length > 6) {
          ids.splice(6, ids.length - 1);
        }
        return ids.join(",");
      },
      findTargetSection(){
        var index = this.targetSectionIndex,id = this.targetSectionId ;

        if(id == this.sections[index].sectionId){
          return this.sections[index];
        }
      },
      $doSection(val){
        if(val === "menu1"){//编辑
          var el = this.findTargetSection();
          this.beforEditSection = el ;
          el.showEditInput = true ;

        }else if(val === "menu2"){//删除
          this.delMaterial(this.targetSectionId,this.targetSectionIndex);
        }
        this.targetSectionId = 0;
        this.sectionIndex = 0;
      },
      $cancelSection(section){


        this.$nextTick(function(){

          section.section = false ;
          var id = this.id , type =this.type ;
          $sectionList.call(this,id,type);
        })

      },
      $doEditSection(section){

        var self = this,
          index = this.targetSectionIndex,
          id = this.targetSectionId ;
        if(section.name == "" ||section.length <=20 ) {
          alert("请填写课程名称");
          this.$emit("modalAlert", "请填写课程名称", "确定");
          return
        }
        if(section.link == "" || !this.reg.isUrl(section.link)) {
          alert("填写素材链接错误");
          this.$emit("modalAlert", "请填写素材链接", "确定");
          return
        }
        var obj = {
          section: {
            activityId: self.data.activityId,
            name: section.name,
            link: section.link,
            type: self.type,
            id  : section.sectionId
          }
        };

        section.showEditInput = false;
        edit.call(this, obj);
      },

      $cancel(){
          this.lessonName = "";
          this.link = "";
      },
      $doEdit(){
        var type = this.type ;
        if(this.lessonName == "") {
          alert("请填写课程名称");
          this.$emit("modalAlert", "请填写课程名称", "确定");
          return
        }
        console.log(!this.reg.isUrl(this.link));
        if(this.link == "" || !this.reg.isUrl(this.link)){
          alert("请填写正确的素材链接");
          this.$emit("modalAlert", "请填写素材链接", "确定");
          return ;
        }
        var obj = {
          section: {
            activityId: this.data.id,
            name: this.lessonName,
            link: this.link,
            type: type
          }
        };

        create.call(this, obj);
      },

      creatMaterial(){

        var type = 'attendance';
        if(this.lessonName == "") {
          alert("请填写课程名称");
          this.$emit("modalAlert", "请填写课程名称", "确定");
          return
        }
        console.log(!this.reg.isUrl(this.link));
        if(this.link == "" || !this.reg.isUrl(this.link)){
          alert("请填写正确的素材链接");
          this.$emit("modalAlert", "请填写素材链接", "确定");
          return ;
        }
        var obj = {
          section: {
            activityId: this.data.id,
            name: this.lessonName,
            link: this.link,
            type: type
          }
        };

        create.call(this, obj);

      },
      delMaterial(sectionId,sectionIndex){
        this.sections.splice(sectionIndex,1);
        var obj = {
          sectionId: sectionId
        }
        del.call(this, obj);
      },
      editMaterial(sectionId,sectionIndex){
        //alert(sectionId)
        //alert(sectionIndex)
        var obj = {
          sectionId: sectionId,
          sectionIndex: sectionIndex
        }
        del.call(this, obj);
      },
      showMaterialMenu(sectionId,sectionIndex){

        this.showMenus = true;
        this.targetSectionId = sectionId;
        this.targetSectionIndex = sectionIndex;
      },
      goBack() {
        var _self = this;
        this.$router.push({path:"/admin/punch",query: {id: _self.id,type : _self.type}});
      },
      doSubmit(){
        //判断是否上传中
        if(this.isUploading){
          this.isUploading = true;
          if(this.descriptionImageUploadData.image.length == 0){
            this.isUploading = false;
          }
        }
        if(this.isUploading) {
//          alert("图片仍未全部上传成功，请稍等");
          return
        }

        this.data.speakerImgIds = this.countSpeakerImgIds();
        this.data.descriptionImgIds = this.countDescriptionImgIds();
        var obj = {attendance: this.data};
        var type = this.type ;
        publish.call(this,obj,type);
        //ajax提交表单,如果成功则跳转,否则弹窗

      }
    },
    watch: {
      'imageUploadData.image'(val){
        this.isUploading = false;
      },
      'descriptionImageUploadData.image'(val){
        this.isUploading = false;
      },
      'speakerImageUploadData.image'(val){
        this.isUploading = false;
      }
    }
  }



